import { Component, TemplateRef, ViewChild } from '@angular/core';
import { EMPTY, Observable, of } from 'rxjs';
import { catchError, switchMap, tap } from 'rxjs/operators';

import { MessageReplyPostRequestBody } from '@backbase/messages-v5-http-ang';

import { Draft } from '../models/conversations.model';
import { AbstractBaseMessagesConversationThreadService } from '../services/abstract-base-messages-conversation-thread.service';
import { SharedMethodsService } from '../../../services/shared-methods/shared-methods.service';

@Component({
  template: '',
})
export class BaseConversationThreadDraftsComponent {
  @ViewChild('draftSaveSuccess') draftSaveSuccessTpl: TemplateRef<any> | undefined;
  @ViewChild('draftDeleteSuccess') draftDeleteSuccessTpl: TemplateRef<any> | undefined;

  draftResponse: Draft | undefined;

  constructor(
    protected readonly apiThreadService: AbstractBaseMessagesConversationThreadService,
    protected readonly shared: SharedMethodsService,
  ) {}

  saveDraft(conversationId: string, body: MessageReplyPostRequestBody): Observable<any> {
    return of(this.draftResponse).pipe(
      switchMap(draft => (draft ? this.updateDraft(conversationId, body) : this.createDraft(conversationId, body))),
      tap(() => this.shared.openNotification(this.draftSaveSuccessTpl, 'success')),
      catchError(error => {
        this.shared.openNotification(this.shared.getErrorMessage(error), 'error');

        return EMPTY;
      }),
    );
  }

  deleteDraft(): Observable<any> {
    if (this.apiThreadService.deleteDraft && this.draftResponse) {
      return this.apiThreadService.deleteDraft(this.draftResponse.id).pipe(
        tap(() => {
          this.draftResponse = undefined;
          this.shared.openNotification(this.draftDeleteSuccessTpl, 'success');
        }),
        catchError(error => {
          this.shared.openNotification(this.shared.getErrorMessage(error), 'error');

          return EMPTY;
        }),
      );
    }

    return of(undefined);
  }

  private createDraft(conversationId: string, body: MessageReplyPostRequestBody): Observable<any | null | undefined> {
    if (this.apiThreadService.createDraft) {
      return this.apiThreadService.createDraft(conversationId, body).pipe(
        tap(
          res =>
            (this.draftResponse = {
              id: res ? res.id : '',
              createdAt: new Date().toString(),
            }),
        ),
      );
    }

    return of(undefined);
  }

  private updateDraft(conversationId: string, body: MessageReplyPostRequestBody): Observable<any> {
    if (this.apiThreadService.updateDraft) {
      return this.apiThreadService.updateDraft(conversationId, body);
    }

    return of(undefined);
  }
}
