import { Component, EventEmitter, Input, OnDestroy, Output, TemplateRef, ViewChild } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { AbstractControl, FormControl, FormGroup } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { BehaviorSubject, combineLatest, EMPTY, Observable, Subject, of } from 'rxjs';
import { catchError, switchMap, takeUntil, tap } from 'rxjs/operators';

import { Metadata, MessageReplyPostRequestBody, MarkAsReadUnreadPutResponseBody } from '@backbase/messages-v5-http-ang';

import { RichTextEditorComponent } from '@backbase/ui-ang/rich-text-editor';

import {
  ManipulationType,
  MessagesConfirmationModalModel,
} from '../../messages-manipulation-confirm-modal/manipulation-confirm-modal-types.model';
import { AbstractBaseMessagesConversationThreadService } from '../services/abstract-base-messages-conversation-thread.service';
import { SharedMethodsService } from '../../../services/shared-methods/shared-methods.service';
import { MessagesManipulationConfirmModalService } from '../../messages-manipulation-confirm-modal/messages-manipulation-confirm-modal.service';
import { MessagesUploadAttachmentsService } from '../../upload-attachments/services/messages-upload-attachments.service';
import { MessagesEncodingService } from '../../../services/messages-encoding/messages-encoding.service';
import { ReadUnreadStatusType } from '../models/conversations.model';
import { BaseConversationThreadDraftsComponent } from './base-conversation-thread-drafts.component';
import { UnknownIdError } from '../../../utils/unknown-id-error';
import { shareReplayOnce } from '../../../helpers/rxjs-operators';

@Component({
  template: '',
})
export class BaseConversationThreadComponent extends BaseConversationThreadDraftsComponent implements OnDestroy {
  static counter = 0;

  /**
   * Event emitted when this thread should be closed or navigation should be changed to previous or another widget
   */
  @Output() back = new EventEmitter();

  /**
   * Selected conversation id setter
   */
  @Input() set selectedConversationId(value: string | undefined) {
    this.conversationId$.next(value);
  }

  /**
   * The notification for the reply success.
   */
  @ViewChild('conversationReplySuccess,messageReplySuccess') replySuccessTpl: TemplateRef<any> | undefined;

  /**
   * The notification for the delete success.
   */
  @ViewChild('conversationDeleteSuccess,messageDeleteSuccess') deleteSuccessTpl: TemplateRef<any> | undefined;

  /**
   * The message text editor.
   */
  @ViewChild('richTextEditor') richTextEditor: RichTextEditorComponent | undefined;

  /**
   * The conversation thread ID.
   */
  readonly id = `base-conversation-thread-component-${BaseConversationThreadComponent.counter++}`;

  /**
   * The manipulation types enum (`resolve`, `unresolve`, `assign`, `unassign`, `discard`,
   * `discardDraft`, `discardPreview`, `deleteTopic`, `deleteMailout`).
   */
  readonly manipulationTypes = ManipulationType;

  protected readonly unsubscribe = new Subject();
  protected readonly replyPressed = new Subject<MessageReplyPostRequestBody>();
  private readonly conversationId$ = new BehaviorSubject<string | undefined>(undefined);

  /**
   * The error information.
   */
  errorResponse: HttpErrorResponse | UnknownIdError | undefined;

  /**
   * The loading flag for the data fetched through the HTTP request.
   */
  loading = true;

  /**
   * The flag defining if the reply is opened or not.
   */
  replyOpened = false;

  /**
   * The flag defining if the topic alert is closed or not.
   */
  topicAlertClosed = false;

  /**
   * The modal for action confirmation.
   */
  confirmationModal: MessagesConfirmationModalModel = {
    opened: false,
    type: undefined,
  };

  /**
   * The reply form.
   */
  replyFormGroup = new FormGroup({
    body: new FormControl(),
    attachments: new FormControl(),
  });

  /**
   * The form loading flag.
   */
  formLoading = false;

  protected readonly conversationId: Observable<string> = this.getConversationId();
  protected readonly markAsReadUnreadSubject = new BehaviorSubject<ReadUnreadStatusType>(ReadUnreadStatusType.READ);
  protected readonly markAsReadUnreadStream: Observable<MarkAsReadUnreadPutResponseBody | null> =
    this.getMarkAsReadUnreadStream();

  constructor(
    protected readonly apiThreadService: AbstractBaseMessagesConversationThreadService,
    protected readonly activatedRoute: ActivatedRoute,
    protected readonly shared: SharedMethodsService,
    readonly confirmModalService: MessagesManipulationConfirmModalService,
    protected readonly uploadService: MessagesUploadAttachmentsService,
    protected readonly encodingService: MessagesEncodingService,
  ) {
    super(apiThreadService, shared);
  }

  /**
   * Conversation id getter
   */
  get conversationIdValue() {
    return this.conversationId$.getValue();
  }

  ngOnDestroy() {
    this.unsubscribe.next();
    this.unsubscribe.complete();
  }

  /**
   * The method to save the draft.
   */
  onSaveDraft() {
    if (this.replyFormGroup.valid) {
      this.saveDraft(this.conversationIdValue ? this.conversationIdValue : '', this.convertFormValue())
        .pipe(takeUntil(this.unsubscribe))
        .subscribe();
    }
  }

  /**
   * The method that is used for component initialization to subscribe for the read-unread status and
   * to toggle the reply form updates.
   */
  initSubscriptions() {
    this.markAsReadUnreadStream.pipe(takeUntil(this.unsubscribe)).subscribe();

    this.confirmModalService.emitToggleModal$.pipe(takeUntil(this.unsubscribe)).subscribe(({ opened, type }) => {
      this.confirmationModal = { opened, type };

      if (!opened && (type === ManipulationType.discard || type === ManipulationType.discardDraft)) {
        this.toggleReplyForm(false);
      }
    });
  }

  /**
   * The method to navigate back.
   */
  navigateBack() {
    this.back.emit();
  }

  /**
   * The method to toggle the reply form.
   *
   * @param flag Toggle status
   */
  toggleReplyForm(flag: boolean) {
    this.replyOpened = flag;

    if (!flag) {
      this.uploadService.reset(true);
      this.replyFormGroup.reset();
    } else {
      setTimeout(() => this.richTextEditor && this.richTextEditor.labelClick());
    }
  }

  /**
   * The method to submit the reply.
   */
  reply() {
    this.replyFormGroup.markAsDirty();
    this.replyFormGroup.markAllAsTouched();
    if (this.replyFormGroup.valid) {
      this.replyPressed.next(this.convertFormValue());
    }
  }

  /**
   * The method that defines whether the control is valid or not.
   *
   * @param field Field name
   * @returns Is form field valid flag
   */
  isInvalidControl(field: string): boolean {
    const control = this.getControl(field);

    return !!control && control.touched && control.invalid;
  }

  /**
   * The method to get any errors generated by failing validation, or null if there are no errors.
   *
   * @param field Field name
   * @param type Error type
   */
  hasError(field: string, type: string): boolean {
    const control = this.getControl(field);

    return control && control.errors && control.errors[type];
  }

  private getConversationId(): Observable<string> {
    return this.conversationId$.pipe(
      switchMap(id => {
        if (!id) {
          this.errorResponse = new UnknownIdError();

          return EMPTY;
        }

        return of(id);
      }),
      shareReplayOnce(),
    );
  }

  private getMarkAsReadUnreadStream(): Observable<MarkAsReadUnreadPutResponseBody | null> {
    return combineLatest([this.markAsReadUnreadSubject, this.conversationId]).pipe(
      switchMap(([status, conversationId]) =>
        this.apiThreadService.markConversationsAsReadUnread({ status, ids: [conversationId] }).pipe(
          tap(() => status === ReadUnreadStatusType.UNREAD && this.navigateBack()),
          catchError(error => {
            this.shared.openNotification(this.shared.getErrorMessage(error), 'error');

            return EMPTY;
          }),
        ),
      ),
    );
  }

  private convertFormValue(): MessageReplyPostRequestBody {
    const { body, attachments } = this.replyFormGroup.value;

    return {
      body: this.encodingService.b64Encode(body),
      ...(attachments?.length && { attachments: (attachments as Metadata[]).map(item => item.id) }),
    };
  }

  private getControl(field: string): AbstractControl | undefined {
    return this.replyFormGroup?.controls[field];
  }
}
