import { Component, Input } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { UnknownIdError } from '../../utils/unknown-id-error';

export declare type MessagesErrorKind =
  | 'MessagesRequestConnectivityError'
  | 'MessagesRequestBadRequest'
  | 'MessagesRequestAccessDenied'
  | 'MessagesRequestNotFound'
  | 'MessagesRequestUnknownIdError'
  | 'MessagesRequestUnknownError';

@Component({
  selector: 'bb-messages-request-error, bb-messages-request-error-ui',
  templateUrl: './messages-request-error.component.html',
})
export class MessagesRequestErrorComponent {
  /**
   * The input that set errorKind from `MessagesErrorKind`
   */
  @Input()
  set error(error: HttpErrorResponse | UnknownIdError) {
    if (error instanceof UnknownIdError) {
      this.errorKind = 'MessagesRequestUnknownIdError';
    } else if (error.error instanceof ErrorEvent) {
      this.errorKind = 'MessagesRequestUnknownError';
    } else {
      switch (error.status) {
        case 0:
          this.errorKind = 'MessagesRequestConnectivityError';
          break;
        case 400:
          this.errorKind = 'MessagesRequestBadRequest';
          break;
        case 401:
          this.errorKind = 'MessagesRequestAccessDenied';
          break;
        case 403:
          this.errorKind = 'MessagesRequestAccessDenied';
          break;
        case 404:
          this.errorKind = 'MessagesRequestNotFound';
          break;
        default:
          this.errorKind = 'MessagesRequestUnknownError';
      }
    }
  }

  /**
   * Error type
   */
  errorKind: MessagesErrorKind | undefined;
}
