import { ChangeDetectorRef, Component, ElementRef, Input, OnDestroy, Renderer2, ViewChild } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { delay, filter, takeUntil, tap } from 'rxjs/operators';

@Component({
  selector: 'bb-messages-body-preview, bb-message-preview',
  templateUrl: './messages-preview.component.html',
})
export class MessagesBodyPreviewComponent implements OnDestroy {
  /**
   * Body from body message
   */
  @Input()
  set body(val: string) {
    this.iframeContext.next(val);
  }

  /**
   * Flag to control visibility
   */
  isVisible = false;

  private readonly iframeContext = new BehaviorSubject<string | undefined>(undefined);
  private readonly destroy = new Subject<void>();

  /**
   * Message preview
   */
  @ViewChild('iframeRef', { static: true }) iframeRef!: ElementRef<HTMLIFrameElement>;

  constructor(private readonly renderer: Renderer2, private readonly cd: ChangeDetectorRef) {}

  ngOnDestroy() {
    this.destroy.next();
  }

  /**
   * Load preview iframe
   */
  iframeLoad() {
    const iframeElement = this.iframeRef.nativeElement;
    const iframeBody = (iframeElement.contentDocument as Document).body;

    this.renderer.setStyle(iframeElement, 'width', '100%');
    this.renderer.setStyle(iframeElement, 'border', '0');
    this.renderer.setStyle(iframeBody, 'margin', '0');

    this.iframeContext
      .pipe(
        filter(Boolean),
        tap(content => this.renderer.setProperty(iframeBody, 'innerHTML', content)),
        delay(300),
        takeUntil(this.destroy),
      )
      .subscribe(() => {
        let currentHeight = iframeBody.scrollHeight;
        let previousHeight = 0;
        let counter = 0;

        /* Several iterations are needed to set iframe height after recalculating template body height
        that contains elements with height or size set in relative units. */
        do {
          this.renderer.setStyle(iframeElement, 'height', `${currentHeight}px`);
          previousHeight = currentHeight;
          currentHeight = iframeBody.scrollHeight;
          counter++;
        } while (previousHeight !== currentHeight && counter < 5);

        this.isVisible = true;
        this.cd.detectChanges();
      });
  }
}
