export enum ManipulationType {
  resolve = 'resolve',
  unresolve = 'unresolve',
  assign = 'assign',
  unassign = 'unassign',
  discard = 'discard',
  discardDraft = 'discardDraft',
  discardPreview = 'discardPreview',
  deleteTopic = 'deleteTopic',
  deleteMailout = 'deleteMailout',
}

export interface MessagesConfirmationModalModel {
  opened: boolean;
  type?: ManipulationType;
}

export interface TopicsConfirmationModalModel extends MessagesConfirmationModalModel {
  topicId?: string | null;
}

export interface MailoutConfirmationModal extends MessagesConfirmationModalModel {
  mailoutId?: string;
}
