import { combineLatest, defer, Observable, of } from 'rxjs';
import { map, publishReplay, refCount } from 'rxjs/operators';

export const getPropertyValue = <T>(input: Observable<T | undefined>, prop: Observable<T>): Observable<T> =>
  combineLatest([input, prop]).pipe(map(([inputVal, propVal]) => (inputVal !== undefined ? inputVal : propVal)));

export const defaultModelObservable = <T>(value: T): Observable<T> =>
  defer(() => of(value)).pipe(publishReplay(1), refCount());
