import { Injectable } from '@angular/core';
import {
  MailoutHttpService,
  MailoutPostRequestBody,
  MailoutPostResponseBody,
  MailoutPreviewResponseBody,
} from '@backbase/messages-v5-http-ang';
import { Observable } from 'rxjs';

@Injectable()
export class MessagesEmployeeMailoutReviewService {
  constructor(private readonly mailoutService: MailoutHttpService) {}

  /**
   * Create mailout
   *
   * @param bodyRequest Create mailout request body
   * @returns Create mailout stream
   */
  createMailout(bodyRequest: MailoutPostRequestBody): Observable<MailoutPostResponseBody | null> {
    return this.mailoutService.postMailout({ mailoutPostRequestBody: bodyRequest });
  }

  /**
   * Sanitize message body
   *
   * @param message Message body
   * @returns Mailout preview response stream
   */
  sanitizeMessageBody(message: string): Observable<MailoutPreviewResponseBody | null> {
    return this.mailoutService.postMailoutPreview({ mailoutPreviewRequestBody: { body: message } });
  }
}
