import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { MessagecenterHttpService, DeleteResponseBody, Metadata } from '@backbase/messages-v5-http-ang';
import { BaseUploadAttachmentsService } from './base-attachments.service';

@Injectable()
export class MessagesClientUploadAttachmentsService implements BaseUploadAttachmentsService {
  constructor(private readonly service: MessagecenterHttpService) {}

  /**
   * Delete attachments by ids
   *
   * @param ids Attachments list
   * @returns Delete attachments stream
   */
  deleteAttachments(ids: Array<string>): Observable<DeleteResponseBody | null> {
    return this.service.deleteMessageAttachments({ ids });
  }

  /**
   * Upload attachments
   *
   * @param attachment Attachment
   * @returns Post message attachments stream
   */
  uploadAttachment(attachment: Blob): Observable<Metadata | null> {
    return this.service.postMessageAttachments({ attachment });
  }
}
