import { Component, Input, OnInit, Directive, Output, EventEmitter, ViewChild, TemplateRef } from '@angular/core';
import { MessagesEmployeeMailoutReviewService } from './employee-mailout-review.service';
import { catchError, map, finalize } from 'rxjs/operators';
import { EMPTY, Observable } from 'rxjs';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { MailoutPostRequestBody } from '@backbase/messages-v5-http-ang';
import { MessagesEncodingService } from '../../../services/messages-encoding/messages-encoding.service';
import { EmployeeCreateMailoutFormModel } from '../../../models/employee-create-mailout-form.model';
import { MessagesManipulationConfirmModalService } from '../../messages-manipulation-confirm-modal/messages-manipulation-confirm-modal.service';
import {
  ManipulationType,
  MessagesConfirmationModalModel,
} from '../../messages-manipulation-confirm-modal/manipulation-confirm-modal-types.model';
import { SharedMethodsService } from '../../../services/shared-methods/shared-methods.service';

@Component({
  selector: 'bb-messages-employee-mailout-review',
  templateUrl: './employee-mailout-review.component.html',
})
export class MessagesEmployeeMailoutReviewComponent implements OnInit {
  /**
   * The input with the create mailout form data.
   */
  @Input() mailout!: EmployeeCreateMailoutFormModel;

  /**
   * The cancel mailout output.
   */
  @Output() cancel = new EventEmitter();

  /**
   * The confirm operation output.
   */
  @Output() confirm = new EventEmitter();

  /**
   * The edit mailout output.
   */
  @Output() edit = new EventEmitter();

  /**
   * The notification for the successful create mailout operation.
   */
  @ViewChild('mailoutCreateSuccess') mailoutCreateSuccessTpl: TemplateRef<any> | undefined;

  /**
   * The notification for the create mailout error.
   */
  @ViewChild('mailoutCreateError') mailoutCreateErrorTpl: TemplateRef<any> | undefined;

  /**
   * The sanitize error flag.
   */
  sanitizeError = false;

  /**
   * The sanitize loading flag.
   */
  sanitizeLoading = false;

  /**
   * The sanitized mailout body.
   */
  sanitizedBody$?: Observable<string | null>;

  /**
   * The manipulation types enum: `resolve`, `unresolve`, `assign`, `unassign`, `discard`,
   * `discardDraft`, `discardPreview`, `deleteTopic`, `deleteMailout`.
   */
  readonly manipulationTypes = ManipulationType;

  /**
   * The confirmation modal model.
   */
  readonly confirmationModal$: Observable<MessagesConfirmationModalModel> = this.confirmModalService.emitToggleModal$;

  constructor(
    private readonly encodingService: MessagesEncodingService,
    private readonly dataService: MessagesEmployeeMailoutReviewService,
    private readonly confirmModalService: MessagesManipulationConfirmModalService,
    private readonly sharedService: SharedMethodsService,
  ) {}

  ngOnInit() {
    if (this.mailout) {
      this.sanitizedBody$ = this.sanitizeBody(this.encodingService.b64Encode(this.mailout.body || ''));
    }
  }

  private sanitizeBody(body: string): Observable<string | null> {
    this.sanitizeLoading = true;

    return this.dataService.sanitizeMessageBody(body).pipe(
      map(data => data && this.encodingService.b64Decode(data.body)),
      catchError(() => {
        this.sanitizeError = true;

        return EMPTY;
      }),
      finalize(() => (this.sanitizeLoading = false)),
    );
  }

  /**
   * The method to switch from the preview to the editing state.
   */
  onEdit() {
    this.edit.emit();
  }

  /**
   * The method to discard the modal.
   *
   * @param discardParam Should open modal flag
   */
  onDiscardModal(discardParam: boolean) {
    this.confirmModalService.toggleModal(
      discardParam,
      (discardParam && this.manipulationTypes.discardPreview) || undefined,
    );
  }

  /**
   * The method to confirm the discard action.
   */
  onConfirmModal() {
    this.confirmModalService.toggleModal(false);
    this.cancel.emit();
  }

  /**
   * The method to save the mailout.
   *
   * @param employeeCreateMailoutFormModel Create mailout form model data
   */
  createMailout({
    body,
    subject,
    topic,
    attachments,
    notDeletable,
    recipientsDataId,
    template,
    important,
    ...rest
  }: EmployeeCreateMailoutFormModel) {
    const finalValue: MailoutPostRequestBody = {
      ...rest,
      subject: '',
      topic: '',
      body: '',
      deletable: !notDeletable,
      important: !!important,
      recipientsData: {
        id: recipientsDataId[0].id,
      },
      ...(body && body.length && { body: this.encodingService.b64Encode(body) }),
      ...(subject && subject.length && { subject: subject }),
      ...(topic && topic.id && { topic: topic.id }),
      ...(attachments && attachments.length && { attachments: attachments.map(({ id }) => id) }),
    };

    this.dataService.createMailout(finalValue).subscribe(
      () => this.sharedService.openNotification(this.mailoutCreateSuccessTpl),
      () => this.sharedService.openNotification(this.mailoutCreateErrorTpl, 'error'),
      () => this.confirm.emit(),
    );
  }
}

@Directive({
  selector: '[bbMessagesEmployeeMailoutReviewCustomizable]',
})
export class MessagesEmployeeMailoutReviewCustomizableDirective extends BbTemplate<
  MessagesEmployeeMailoutReviewComponent,
  {
    sanitizedBody: string | null;
    confirmationModal: MessagesConfirmationModalModel;
  }
> {}

@Directive({
  selector: '[bbMessagesEmployeeMailoutReviewSuccessNotificationHeaderCustomizable]',
})
export class MessagesEmployeeMailoutReviewSuccessNotificationHeaderCustomizableDirective extends BbTemplate<
  MessagesEmployeeMailoutReviewComponent,
  void
> {}

@Directive({
  selector: '[bbMessagesEmployeeMailoutReviewErrorNotificationHeaderCustomizable]',
})
export class MessagesEmployeeMailoutReviewErrorNotificationHeaderCustomizableDirective extends BbTemplate<
  MessagesEmployeeMailoutReviewComponent,
  void
> {}
