import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { catchError } from 'rxjs/operators';
import {
  EmployeeHttpService,
  MailoutHttpService,
  MailoutPostRequestBody,
  MailoutPostResponseBody,
  TopicsGetEmployeeResponseBody,
} from '@backbase/messages-v5-http-ang';
import { BaseCreateMessageFormService } from '../base-form/base-create-message-form.service';

@Injectable()
export class EmployeeCreateMailoutFormService implements BaseCreateMessageFormService<MailoutPostRequestBody> {
  private readonly maxPaginationItemsNum = 65535;

  constructor(
    private readonly messagesService: MailoutHttpService,
    private readonly employeeService: EmployeeHttpService,
  ) {}

  /**
   * Get topics
   *
   * @returns Get topics stream
   */
  getTopics(): Observable<TopicsGetEmployeeResponseBody[] | null> {
    return this.employeeService
      .getTopicsEmployee({ size: this.maxPaginationItemsNum, subscribed: true })
      .pipe(catchError(() => of([])));
  }

  /**
   * Create message
   *
   * @param body Create message request body
   * @returns Create message stream
   */
  createMessage(body: MailoutPostRequestBody): Observable<MailoutPostResponseBody | null> {
    return this.messagesService.postMailout({ mailoutPostRequestBody: body });
  }
}
