import { Inject, Injectable } from '@angular/core';
import { merge, Observable, of, Subject, timer } from 'rxjs';
import { concatMap, map, switchMap } from 'rxjs/operators';
import { MessagecenterHttpService, UnreadConversationsGetResponseBody } from '@backbase/messages-v5-http-ang';
import { PUBSUB, Pubsub } from '@backbase/foundation-ang/web-sdk';

export interface Badges {
  token: string;
  count: number;
}

const MessagesToken = 'messages';
const MessagesPollPeriod = 5000;
const bbEventUnreadConversationCountUpdate = 'bb.event.unread-conversation-count.update';

@Injectable()
export class MessagesUnreadConversationCountService {
  private readonly resetSubject = new Subject();
  private readonly subscriptionHandler = this.reset.bind(this);

  constructor(
    private readonly messagecenterService: MessagecenterHttpService,
    @Inject(PUBSUB) private readonly pubSub: Pubsub,
  ) {
    this.pubSub.subscribe(bbEventUnreadConversationCountUpdate, this.subscriptionHandler);
  }

  /**
   * Conversations count poll source
   */
  source() {
    return (): Observable<Badges> =>
      merge(this.resetSubject, of(undefined)).pipe(
        switchMap(() =>
          timer(0, MessagesPollPeriod).pipe(
            concatMap(() => this.getUnreadConversationCount()),
            map(body => ({ token: MessagesToken, count: body ? body.unreadCount : 0 })),
          ),
        ),
      );
  }

  /**
   * Get conversations count
   *
   * @returns Get conversations count stream
   */
  getUnreadConversationCount(): Observable<UnreadConversationsGetResponseBody | null> {
    return this.messagecenterService.getUnreadConversationsCount();
  }

  private reset() {
    this.resetSubject.next();
  }
}
