import { ChangeDetectorRef, Component, Directive, Input } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, ValidationErrors } from '@angular/forms';
import { catchError, debounceTime, distinctUntilChanged, filter, switchMap, tap } from 'rxjs/operators';
import { EMPTY, Observable } from 'rxjs';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { GetIdentity, IdentityListedItem } from '@backbase/data-ang/user';
import {
  PresentationMessageEmployeePostRequestBody,
  TopicsGetEmployeeResponseBody,
} from '@backbase/messages-v5-http-ang';
import { BaseCreateMessageFormComponent } from '../base-form/base-create-message-form.component';
import { MessagesEncodingService } from '../../../services/messages-encoding/messages-encoding.service';
import { SharedMethodsService } from '../../../services/shared-methods/shared-methods.service';
import { MessagesManipulationConfirmModalService } from '../../messages-manipulation-confirm-modal/messages-manipulation-confirm-modal.service';
import { BaseCreateMessageFormService } from '../base-form/base-create-message-form.service';
import { EmployeeCreateMessageFormService } from './services/employee-create-message-form.service';
import { MessagesUploadAttachmentsService } from '../../upload-attachments/services/messages-upload-attachments.service';
import { CreateMessageFormImplementationService } from './services/create-message-form-implementation.service';
import { BaseUploadAttachmentsService } from '../../upload-attachments/services/base-attachments.service';
import { MessagesEmployeeUploadAttachmentsService } from '../../upload-attachments/services/employee-upload-attachments.service';
import { topicPlaceholderConfig } from '../../../utils/topic-placeholder-text-helper';

@Component({
  selector: 'bb-messages-employee-create-message-form',
  templateUrl: './employee-create-message-form.component.html',
  providers: [
    {
      provide: BaseCreateMessageFormService,
      useClass: CreateMessageFormImplementationService,
    },
    {
      provide: BaseUploadAttachmentsService,
      useClass: MessagesEmployeeUploadAttachmentsService,
    },
  ],
})
export class EmployeeCreateMessageFormComponent extends BaseCreateMessageFormComponent<PresentationMessageEmployeePostRequestBody> {
  /**
   * Debounce time for input search requests
   */
  @Input() debounceTime = 500;

  /**
   * Minimum length value for input search request
   */
  @Input() minSearchLength = 1;

  /**
   * Predefine form
   */
  @Input() set preDefineForm(val: Partial<PresentationMessageEmployeePostRequestBody>) {
    if (val.recipient) {
      this.getUserName(val.recipient);
    }
  }

  /**
   * Make predefined user field readonly
   */
  @Input() isPreDefinedUserReadonly = false;

  /**
   * Configs for topic selector placeholder
   */
  public readonly topicPlaceholderConfig = topicPlaceholderConfig;

  /**
   * Typeahead options.
   */
  readonly typeaheadOptions = {
    ngbTypeahead: this.search.bind(this),
    resultFormatter: (item: IdentityListedItem) => item.fullName,
    inputFormatter: (item: IdentityListedItem) => item.fullName,
  };

  /**
   * Additional form fields.
   */
  readonly additionalFormFields: { [key: string]: boolean | string } = {
    recipient: '',
    important: false,
    notDeletable: false,
    'read-only': false,
  };

  constructor(
    private readonly employeeService: EmployeeCreateMessageFormService,
    readonly encodingService: MessagesEncodingService,
    readonly sharedService: SharedMethodsService,
    readonly fb: FormBuilder,
    readonly cd: ChangeDetectorRef,
    readonly createMessageFormService: BaseCreateMessageFormService<PresentationMessageEmployeePostRequestBody>,
    readonly confirmModalService: MessagesManipulationConfirmModalService,
    readonly uploadService: MessagesUploadAttachmentsService,
  ) {
    super(sharedService, confirmModalService, cd, createMessageFormService, fb, encodingService, uploadService);
    Object.keys(this.additionalFormFields).forEach(key => {
      const validator = key === 'recipient' ? this.customRequiredValidator : undefined;
      this.newMessageFormGroup.addControl(key, new FormControl(this.additionalFormFields[key], validator));
    });

    this.newMessageFormGroup.updateValueAndValidity();
  }

  /**
   * The method upon creation of the message form submission.
   */
  onConfirm() {
    this.newMessageFormGroup.markAsDirty();
    this.newMessageFormGroup.markAllAsTouched();

    const { valid } = this.newMessageFormGroup;
    const value = this.newMessageFormGroup.getRawValue();
    if (valid) {
      const finalValue: PresentationMessageEmployeePostRequestBody = {
        subject: '',
        body: '',
        topic: '',
        recipient: value.recipient.id,
        important: value.important,
        deletable: !value.notDeletable,
        'read-only': value['read-only'],
        ...this.getSimpleFormVal(value),
      };
      this.createMessage(finalValue);
    }
  }

  /**
   * The method to search for typeahead options.
   *
   * @param text Search query
   * @returns Search stream
   */
  search(text: Observable<string>): Observable<IdentityListedItem[] | Record<string, never>> {
    return text.pipe(
      debounceTime(this.debounceTime),
      distinctUntilChanged(),
      filter(value => value.length > this.minSearchLength),
      switchMap(value => this.employeeService.getUsers(value)),
    );
  }

  protected resetToInit() {
    super.resetToInit();
    this.resetAdditionalFields();
  }

  private customRequiredValidator(control: AbstractControl): ValidationErrors | null {
    const value = control.value;
    const isSelected = value && typeof value !== 'string';

    return !isSelected ? { required: true } : null;
  }

  private getUserName(userId: string) {
    this.employeeService
      .getUserById(userId)
      .pipe(
        tap(user => this.setUserName(user, userId)),
        catchError(err => {
          this.sharedService.openNotification(this.sharedService.getErrorMessage(err), 'error');

          return EMPTY;
        }),
      )
      .subscribe();
  }

  private setUserName(user: GetIdentity, userId: string) {
    this.newMessageFormGroup.controls['recipient'].setValue({ ...user, id: userId });
    if (this.isPreDefinedUserReadonly) {
      this.newMessageFormGroup.controls['recipient'].disable();
    }
  }

  private resetAdditionalFields() {
    Object.keys(this.additionalFormFields).forEach(key => {
      this.newMessageFormGroup.controls[key].setValue(this.additionalFormFields[key]);
    });
  }
}

@Directive({
  selector: '[bbEmployeeCreateMessageFormCustomizable]',
})
export class EmployeeCreateMessageFormCustomizableDirective extends BbTemplate<
  EmployeeCreateMessageFormComponent,
  { topics: TopicsGetEmployeeResponseBody[] }
> {}
