import { Observable, Subject } from 'rxjs';
import { DeleteResponseBody, Metadata } from '@backbase/messages-v5-http-ang';
import { takeUntil, tap } from 'rxjs/operators';
import { FileObject } from '../model/messages-upload-attachments.model';

export abstract class BaseUploadAttachmentsService {
  abstract deleteAttachments(ids: Array<string>): Observable<DeleteResponseBody | null>;
  abstract uploadAttachment(attachment: Blob): Observable<Metadata | null>;
}

export interface FileAttachmentInterface {
  loading: boolean;
  name: string | undefined;
  size: number | undefined;
  onDelete: Subject<number>;
  deleteAttachment(id: string, index: number): void;
  uploadAttachment(): Observable<Metadata | null>;
  setLoading(flag: boolean): void;
}

export class FileAttachment implements FileAttachmentInterface {
  /**
   * The loading flag.
   */
  loading = false;

  /**
   * Attachment name
   */
  name: string | undefined;

  /**
   * Attachment size
   */
  size: number | undefined;

  /**
   * Delete attachment subject that holds index
   */
  onDelete = new Subject<number>();

  private readonly cancelSubject = new Subject<void>();
  private readonly file: File | FileObject | undefined;
  private id: string | undefined;

  constructor(file: File | FileObject, protected service: BaseUploadAttachmentsService) {
    this.file = file;
    this.size = file.size;

    if ((file as FileObject).id) {
      this.id = (file as FileObject).id;
      this.name = (file as FileObject).filename;
    } else {
      this.name = (file as File).name;
    }
  }

  /**
   * Delete attachment
   *
   * @param id Attachment id
   * @param index Attachment index
   */
  deleteAttachment(id: string, index: number) {
    if (this.id && this.id === id) {
      this.service.deleteAttachments([id]).subscribe();
    } else {
      this.cancelSubject.next();
    }

    this.onDelete.next(index);
  }

  /**
   * Upload attachments
   */
  uploadAttachment() {
    this.setLoading(true);

    return this.service.uploadAttachment(this.file as Blob).pipe(
      takeUntil(this.cancelSubject),
      tap(value => {
        this.id = value ? value.id : undefined;
        this.setLoading(false);
      }),
    );
  }

  /**
   * Set loading flag
   *
   * @param flag Loaging status flag
   */
  setLoading(flag: boolean) {
    this.loading = flag;
  }
}
