import { Observable } from 'rxjs';

export interface Attachment {
  id: string;
  filename: string;
  size: number;
  createdAt: string;
  readonly link?: string;
  readonly linkStream?: Observable<ArrayBuffer>;
}
export enum ReadUnreadStatusType {
  READ = 'READ',
  UNREAD = 'UNREAD',
}

export interface Draft {
  id: string;
  body?: string;
  createdAt: string;
  attachments?: Array<{
    id: string;
    filename: string;
    size: number;
    createdAt: string;
  }>;
}
