import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { DeleteResponseBody, EmployeeHttpService, Metadata } from '@backbase/messages-v5-http-ang';
import { BaseUploadAttachmentsService } from './base-attachments.service';

@Injectable()
export class MessagesEmployeeUploadAttachmentsService implements BaseUploadAttachmentsService {
  constructor(private readonly service: EmployeeHttpService) {}

  /**
   * Delete attachments by ids
   *
   * @param ids attachments ids
   * @returns Delete attachments stream
   */
  deleteAttachments(ids: string[]): Observable<DeleteResponseBody | null> {
    return this.service.deleteMessageAttachmentsEmployee({ ids });
  }

  /**
   * Upload attachment
   *
   * @param attachment Attachment data
   * @returns Upload attachment stream
   */
  uploadAttachment(attachment: Blob): Observable<Metadata | null> {
    return this.service.postMessageAttachmentsEmployee({ attachment });
  }
}
