import { Component, Directive, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { BehaviorSubject, combineLatest, EMPTY, Observable, Subject } from 'rxjs';
import { catchError, debounceTime, distinctUntilChanged, map, switchMap, take, takeUntil, tap } from 'rxjs/operators';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { IdentityListedItem } from '@backbase/data-ang/user';
import { TopicsGetResponseBody } from '@backbase/messages-v5-http-ang';
import { FilterService } from '../model/filter-service.model';
import { ApplyFilterParams } from '../model/params.model';
import { SharedMethodsService } from '../../../services/shared-methods/shared-methods.service';
import { topicPlaceholderConfig } from '../../../utils/topic-placeholder-text-helper';

@Component({
  selector: 'bb-messages-filter-conversation-list',
  templateUrl: './filter-conversations-list.component.html',
})
export class FilterConversationsListComponent implements OnDestroy, OnInit {
  /**
   * Css classes for header block
   */
  @Input() headerClass: string | undefined;
  /**
   * Css classes for body block
   */
  @Input() bodyClass: string | undefined;

  /**
   * Observable filter params to init component with
   */
  @Input() set filterParams(value: ApplyFilterParams | undefined) {
    if (value && this.checkFilterParams(this.filterParams$.getValue(), value)) {
      this.applied = true;
      this.filterParams$.next(value);
    }
  }
  /**
   * Configs for topic selector placeholder
   */
  public readonly topicPlaceholderConfig = topicPlaceholderConfig;

  /**
   * Event emitter for apply action
   */
  @Output() apply = new EventEmitter<ApplyFilterParams>();
  /**
   * Event emitter for clear action
   */
  @Output() clear = new EventEmitter<void>();

  /**
   * The filters form reference.
   */
  filterForm = this.fb.group({
    date: [''],
    topic: [''],
    user: [''],
  });

  /**
   * The applied flag.
   */
  applied = false;

  /**
   * The opened flag.
   */
  opened = false;

  /**
   * The stream with the topic list.
   */
  readonly topics$ = new Subject<TopicsGetResponseBody[] | null>();

  /**
   * The method to get the typeahead options.
   */
  readonly typeaheadOptions = {
    ngbTypeahead: this.searchUsers.bind(this),
    resultFormatter: (item: IdentityListedItem) => item.fullName,
    inputFormatter: (item: IdentityListedItem) => item.fullName,
  };

  private readonly filterParams$ = new BehaviorSubject<ApplyFilterParams>({});
  private readonly unsubscribe$ = new Subject<void>();

  constructor(
    private readonly service: FilterService,
    private readonly fb: FormBuilder,
    private readonly shared: SharedMethodsService,
  ) {}

  ngOnInit() {
    combineLatest([this.filterParams$, this.topics$])
      .pipe(
        switchMap(([params, topics]) => {
          const { topicId, endDate, startDate, userId } = params;

          if (topics && topicId) {
            this.filterForm.controls['topic'].setValue(
              topics.find((topic: TopicsGetResponseBody) => topic.id === topicId),
            );
          }

          if (startDate && endDate) {
            this.filterForm.controls['date'].setValue({
              from: new Date(startDate).toISOString(),
              to: new Date(endDate).toISOString(),
            });
          }

          if (userId && this.service.getUserById) {
            return this.service
              .getUserById(userId)
              .pipe(tap(user => user && this.filterForm.controls['user'].setValue({ ...user, id: params.userId })));
          }

          return EMPTY;
        }),
        takeUntil(this.unsubscribe$),
      )
      .subscribe();

    this.service
      .getTopics()
      .pipe(take(1))
      .subscribe(topics => {
        this.topics$.next(topics);
        this.topics$.complete();
      });
  }

  /**
   * The method to submit the form.
   */
  submit() {
    const applyObject: ApplyFilterParams = this.convertToFilterParams(this.filterForm);

    this.applied = !!Object.keys(applyObject).length;
    this.opened = false;
    this.apply.emit(applyObject);
  }

  /**
   * The method to reset the form.
   *
   * @param apply Flag to submit or not the form
   */
  reset(apply = false) {
    if (apply) {
      this.filterForm.valueChanges.pipe(take(1)).subscribe(() => {
        this.submit();
      });
    }

    this.filterForm.reset();
  }

  /**
   * The method to open or close the filter form.
   */
  triggerOpened() {
    this.opened = !this.opened;
  }

  ngOnDestroy() {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  private convertToFilterParams(form: FormGroup): ApplyFilterParams {
    return {
      ...(form.value.topic && { topicId: form.value.topic.id }),
      ...(form.value.user && { userId: form.value.user.id }),
      ...(form.value.date && {
        startDate: this.formatDate(form.value.date.from),
        endDate: this.formatDate(form.value.date.to),
      }),
    };
  }

  private formatDate(date: Date): string {
    const d = new Date(date);
    const year = d.getFullYear();
    const month = d.getMonth() + 1;
    const dt = d.getDate();
    const finalDt = dt < 10 ? `0${dt}` : dt.toString();
    const finalMonth = month < 10 ? `0${month}` : month.toString();

    return `${year}-${finalMonth}-${finalDt}`;
  }

  private checkFilterParams(curr: Record<string, any>, next: Record<string, any>): boolean {
    if (!Object.keys(next).length || !(next && (next.userId || next.topicId || (next.startDate && next.endDate)))) {
      return false;
    }

    for (const key in next) {
      if (curr[key] && curr[key] === next[key]) {
        return false;
      }
    }

    return true;
  }

  private searchUsers(text: Observable<string>): Observable<IdentityListedItem[] | null | undefined> {
    return text.pipe(
      debounceTime(500),
      distinctUntilChanged(),
      switchMap(value => {
        if (!this.service.getUsers || !value.length) {
          return EMPTY;
        } else {
          return this.service.getUsers(value).pipe(
            map(response => response && response.identities),
            catchError(err => {
              this.shared.openNotification(this.shared.getErrorMessage(err), 'error');

              return EMPTY;
            }),
          );
        }
      }),
    );
  }
}

@Directive({
  selector: '[bbFilterConversationsListCustomizable]',
})
export class FilterConversationsListCustomizableDirective extends BbTemplate<
  FilterConversationsListComponent,
  { topics: TopicsGetResponseBody[] }
> {}
