import { Inject, Injectable } from '@angular/core';
import { merge, Observable, of, Subject, timer } from 'rxjs';
import { concatMap, switchMap } from 'rxjs/operators';
import { ConversationCounterResponseBody, EmployeeHttpService } from '@backbase/messages-v5-http-ang';
import { PUBSUB, Pubsub } from '@backbase/foundation-ang/web-sdk';

const MessagesPollPeriod = 5000;
const bbEventUnreadConversationCountUpdate = 'bb.event.unread-conversation-count.update';

@Injectable()
export class MessagesEmployeeConversationCountersService {
  private readonly resetSubject = new Subject();
  private readonly subscriptionHandler = this.reset.bind(this);

  constructor(private readonly service: EmployeeHttpService, @Inject(PUBSUB) private readonly pubSub: Pubsub) {
    this.pubSub.subscribe(bbEventUnreadConversationCountUpdate, this.subscriptionHandler);
  }

  /**
   * Conversations count poll source
   */
  source() {
    return (): Observable<ConversationCounterResponseBody | null> =>
      merge(this.resetSubject, of(undefined)).pipe(
        switchMap(() => timer(0, MessagesPollPeriod).pipe(concatMap(() => this.getConversationCounters()))),
      );
  }

  /**
   * Get conversations count
   *
   * @returns Get conversation counters stream
   */
  getConversationCounters(): Observable<ConversationCounterResponseBody | null> {
    return this.service.getConversationsCounter();
  }

  private reset() {
    this.resetSubject.next();
  }
}
