import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { catchError } from 'rxjs/operators';
import {
  EmployeeHttpService,
  TopicsGetResponseBody,
  PresentationMessageEmployeePostRequestBody,
  PresentationMessagePostResponseBody,
} from '@backbase/messages-v5-http-ang';
import { BaseCreateMessageFormService } from '../../base-form/base-create-message-form.service';

@Injectable()
export class CreateMessageFormImplementationService
  implements BaseCreateMessageFormService<PresentationMessageEmployeePostRequestBody>
{
  private readonly maxPaginationItemsNum = 65535;

  constructor(private readonly employeeService: EmployeeHttpService) {}

  /**
   * Get topics
   *
   * @returns Employee topics stream
   */
  getTopics(): Observable<TopicsGetResponseBody[] | null> {
    return this.employeeService
      .getTopicsEmployee({ size: this.maxPaginationItemsNum, subscribed: true })
      .pipe(catchError(() => of([])));
  }

  /**
   * Create message
   *
   * @param params Create message request params
   * @returns Create message stream
   */
  createMessage(
    params: PresentationMessageEmployeePostRequestBody,
  ): Observable<PresentationMessagePostResponseBody | null> {
    return this.employeeService.postMessagesEmployee({ presentationMessageEmployeePostRequestBody: params });
  }
}
