import { Injectable } from '@angular/core';
import { GetIdentities, GetIdentity, IdentityListedItem, IdentityManagementService } from '@backbase/data-ang/user';
import { catchError, map } from 'rxjs/operators';
import { Observable, of } from 'rxjs';

@Injectable()
export class EmployeeCreateMessageFormService {
  constructor(private readonly userService: IdentityManagementService) {}

  getUsers(externalId: string): Observable<IdentityListedItem[] | Record<string, never>> {
    return this.userService.getIdentities({ externalId }).pipe(
      map((usersData: GetIdentities) => usersData.identities),
      catchError(() => of({})),
    );
  }

  getUserById(internalId: string): Observable<GetIdentity> {
    return this.userService.getIdentity({ internalId });
  }
}
