import { Component, Directive, Input, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { Attachment } from '../../models/attachment.model';
import { BbTemplate } from '@backbase/foundation-ang/core';

/**
 * @name MessagesConversationMessageComponent
 *
 * @description
 * Component displays conversation message.
 */
@Component({
  selector: 'bb-messages-conversation-message, bb-message-ui',
  templateUrl: './messages-conversation-message.component.html',
})
export class MessagesConversationMessageComponent implements OnInit {
  private static counter = 0;

  /**
   * Datetime of the message creation, format date-time
   */
  @Input() created: string | undefined;

  /**
   * Text of the message, could have HTML-tags
   */
  @Input('message')
  set message(value: string | undefined) {
    this.messageText = value;
    this.messageTextWithoutTags = value ? this.replaceTags(value) : undefined;
  }

  /**
   * Senders name or templateRef
   */
  @Input() sender: string | TemplateRef<any> | undefined;
  /**
   * Toggles message collapse (text-truncate and replacement HTML-tags)
   */
  @Input() opened = false;
  /**
   * Array of the attached files
   */
  @Input() attachments: Attachment[] | undefined;

  /**
   * Is message body contains html
   */
  @Input() isBodyHtml = false;

  /**
   * Default sender template
   */
  @ViewChild('defaultSenderTemplate', { static: true }) defaultSenderTemplate: TemplateRef<any> | undefined;

  /**
   * Message text
   */
  messageText: string | undefined;

  /**
   * Message text without tags
   */
  messageTextWithoutTags: string | undefined;

  /**
   * The unique ID HTML property for each widget component instance.
   */
  messageId = `messages-conversation-message-${MessagesConversationMessageComponent.counter++}`;

  /**
   * Toggle message on click
   *
   * @param event Click event
   */
  onClick(event: any) {
    const selection = document.getSelection();

    if (
      (selection && selection.toString().length) ||
      (event.target.tagName.toLowerCase() === 'a' && event.target.href)
    ) {
      return;
    }

    this.opened = !this.opened;
  }

  /**
   * Get sender template
   *
   * @returns Sender template
   */
  getSenderTemplate(): TemplateRef<any> | undefined {
    if (this.sender instanceof TemplateRef) {
      return this.sender;
    }

    return this.defaultSenderTemplate;
  }

  ngOnInit() {
    if (typeof this.messageText === 'undefined') {
      throw new Error(`"message" input is required in "${this.constructor.name}"`);
    }
  }

  private readonly replaceTags = (content: string) => (content ? content.replace(/<[^>]+>/gi, '') : '');
}

@Directive({
  selector: '[bbMessagesConversationMessageCustomizable]',
})
export class MessagesConversationMessageCustomizableDirective extends BbTemplate<
  MessagesConversationMessageComponent,
  undefined
> {}
