import { Component, Input, OnDestroy } from '@angular/core';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { BaseCreateMessageModalPropertiesService } from './base-create-message-modal-properties.service';
import { SharedMethodsService } from '../../../services/shared-methods/shared-methods.service';
import { switchMap, takeUntil } from 'rxjs/operators';
import { snapshot } from '../../../utils/snapshot';
import { getPropertyValue } from '../../../utils/item-model-property';
import { modelItemToBoolean } from '../../../utils/type-helpers';

@Component({
  template: '',
})
export class BaseCreateMessageModalComponent implements OnDestroy {
  static counter = 0;

  /**
   * The unique ID HTML property for each component instance.
   */
  readonly id = `base-create-message-modal-${BaseCreateMessageModalComponent.counter++}`;
  private readonly resetSubject = new Subject<undefined>();

  /**
   * The stream with the form reset triggers.
   */
  readonly resetStream = this.resetSubject.asObservable();
  readonly hostRef = this;

  /**
   * The stream with the maximum attachment size.
   */
  @Input('maxAttachmentSize') set maxAttachmentSizeValue(value: string | number) {
    this.maxAttachmentSize$$.next(String(value));
  }

  /**
   * The stream with the maximum name length.
   */
  @Input('maxMailoutNameLength') set maxMailoutNameLengthValue(value: string | number) {
    this.maxMailoutNameLength$$.next(Number(value));
  }

  /**
   * The stream with the maximum subject length.
   */
  @Input('maxSubjectLength') set maxSubjectLengthValue(value: string | number) {
    this.maxSubjectLength$$.next(Number(value));
  }

  /**
   * The stream with the maximum message length.
   */
  @Input('maxMessageLength') set maxMessageLengthValue(value: string | number) {
    this.maxMessageLength$$.next(Number(value));
  }

  /**
   * The stream with the modal header.
   */
  @Input('modalHeader') set modalHeaderValue(value: string) {
    this.modalHeader$$.next(value);
  }

  /**
   * The stream with a flag indicating if the Compose button is hidden.
   */
  @Input('hideComposeButton') set hideComposeButtonValue(value: string | boolean) {
    this.hideComposeButton$$.next(modelItemToBoolean(value));
  }

  /**
   * Event published to open Create message modal
   */
  @Input('createMessageOpenEventName') set createMessageOpenEventNameValue(value: string) {
    this.createMessageOpenEventName$$.next(value);
  }

  /**
   * Event published when Create message modal widget was closed
   */
  @Input('createMessageClosedEventName') set createMessageClosedEventNameValue(value: string) {
    this.createMessageClosedEventName$$.next(value);
  }

  /**
   * The draft ID.
   */
  draftId: string | undefined;

  /**
   * The flag indicating if the modal is opened or closed.
   */
  modalOpened = false;

  /**
   * The modal options.
   */
  modalOptions = {
    size: 'md',
    beforeDismiss: () => {
      this.resetSubject.next();

      return false;
    },
  };

  private readonly maxAttachmentSize$$ = new BehaviorSubject<string | undefined>(undefined);
  private readonly maxMailoutNameLength$$ = new BehaviorSubject<number | undefined>(undefined);
  private readonly maxSubjectLength$$ = new BehaviorSubject<number | undefined>(undefined);
  private readonly maxMessageLength$$ = new BehaviorSubject<number | undefined>(undefined);
  private readonly modalHeader$$ = new BehaviorSubject<string | undefined>(undefined);
  private readonly hideComposeButton$$ = new BehaviorSubject<boolean | undefined>(undefined);
  private readonly createMessageOpenEventName$$ = new BehaviorSubject<string | undefined>(undefined);
  private readonly createMessageClosedEventName$$ = new BehaviorSubject<string | undefined>(undefined);

  private readonly destroy$ = new Subject<void>();

  /**
   * The stream with the maximum name size.
   */
  readonly maxMailoutNameLength: Observable<number> = getPropertyValue(
    this.maxMailoutNameLength$$,
    this.properties.maxMailoutNameLength,
  );

  /**
   * The stream with the maximum subject size.
   */
  readonly maxSubjectLength: Observable<number> = getPropertyValue(
    this.maxSubjectLength$$,
    this.properties.maxSubjectLength,
  );

  /**
   * The stream with the maximum attachment size.
   */
  readonly maxAttachmentSize: Observable<string> = getPropertyValue(
    this.maxAttachmentSize$$,
    this.properties.maxAttachmentSize,
  );

  /**
   * The stream with the maximum message length.
   */
  readonly maxMessageLength: Observable<number> = getPropertyValue(
    this.maxMessageLength$$,
    this.properties.maxMessageLength,
  );

  /**
   * The stream with the modal header text.
   */
  readonly modalHeader: Observable<string> = getPropertyValue(this.modalHeader$$, this.properties.modalHeader);

  /**
   * The stream with a flag indicating if the Compose button is hidden.
   */
  readonly hideComposeButton: Observable<boolean> = getPropertyValue(
    this.hideComposeButton$$,
    this.properties.hideComposeButton,
  );

  /**
   * Event published to open Create message modal
   */
  readonly createMessageOpenEventName: Observable<string> = getPropertyValue(
    this.createMessageOpenEventName$$,
    this.properties.createMessageOpenEventName,
  );

  /**
   * Event published when Create message modal widget was closed
   */
  readonly createMessageClosedEventName: Observable<string> = getPropertyValue(
    this.createMessageClosedEventName$$,
    this.properties.createMessageClosedEventName,
  );

  constructor(protected properties: BaseCreateMessageModalPropertiesService, protected shared: SharedMethodsService) {
    this.createMessageOpenEventName
      .pipe(
        switchMap(eventName => this.shared.eventBusFactory(eventName)),
        takeUntil(this.destroy$),
      )
      .subscribe(obj => this.openModal(obj && obj.draftId));
  }

  /**
   * The method to open the modal.
   *
   * @param id
   */
  openModal(id?: string) {
    this.modalOpened = true;
    this.draftId = id;
  }

  /**
   * The method to close the modal.
   */
  closeModal() {
    this.modalOpened = false;

    const eventName = snapshot(this.createMessageClosedEventName) as string;
    this.shared.eventBusPublish<Record<string, unknown>>(eventName, {});
    this.draftId = undefined;
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
