import { Inject, Injectable, TemplateRef } from '@angular/core';
import { HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { FormGroup } from '@angular/forms';
import { Observable } from 'rxjs';

import { NotificationService } from '@backbase/ui-ang/notification';
import { Pubsub, PUBSUB } from '@backbase/foundation-ang/web-sdk';

@Injectable()
export class SharedMethodsService {
  constructor(
    private readonly notificationService: NotificationService,
    @Inject(PUBSUB) private readonly pubSub: Pubsub,
  ) {}

  /**
   * Check is form valid
   *
   * @param form Form object
   * @param prevValue Previous value
   * @returns Is form valid flag
   */
  isFormInvalid(form: FormGroup | undefined, prevValue?: { [key: string]: any }): boolean {
    if (form && form.dirty) {
      const formVal = form.value || {};
      const keys = Object.keys(formVal);

      for (const field of keys) {
        if (prevValue) {
          if (prevValue[field] !== formVal[field]) {
            return true;
          }
        } else if (formVal[field]) {
          return Array.isArray(formVal[field]) ? formVal[field].length > 0 : true;
        }
      }
    }

    return false;
  }

  /**
   * Check if form has value
   *
   * @param form Form object
   * @returns Has form value flag
   */
  hasFormValue(form: FormGroup | undefined): boolean {
    const formVal = (form && form.value) || {};
    const keys = Object.keys(formVal);

    for (const field of keys) {
      if (formVal[field]) {
        return Array.isArray(formVal[field]) ? formVal[field].length > 0 : true;
      }
    }

    return false;
  }

  /**
   * Handles a given event
   *
   * @param eventName Event name
   */
  eventBusFactory(eventName: string): Observable<any> {
    return new Observable(observer => {
      const handler = (data: any) => observer.next(data);
      this.pubSub.subscribe(eventName, handler);

      return () => {
        this.pubSub.unsubscribe(eventName, handler);
      };
    });
  }

  /**
   * Publish a given event
   *
   * @template T
   * @param eventName Event name
   * @param body Event data
   */
  eventBusPublish<T>(eventName: string, body: T) {
    this.pubSub.publish(eventName, body);
  }

  /**
   * Get error message from error body
   *
   * @param error Error value
   * @returns Error message
   */
  getErrorMessage(error: HttpErrorResponse | string): string {
    return typeof error !== 'string' ? (error.error && error.error.message) || error.message : error;
  }

  /**
   * Open notification
   *
   * @param header Notification header
   * @param modifier Notification type
   * @param message Notification message
   */
  openNotification(header: TemplateRef<any> | string | undefined, modifier = 'success' as any, message = '') {
    if (!header) return;
    this.notificationService.showNotification({
      header,
      message,
      modifier,
      dismissible: false,
    });
  }

  /**
   * Map response to take body and total count
   *
   * @template T
   * @param rawData Response data
   * @returns Response body and total count
   */
  processData<T>(rawData: HttpResponse<T>): { data: T | []; totalCount: number } {
    const totalCount: number = (rawData && parseInt(String(rawData.headers.get('x-total-count')), 10)) || 0;

    return {
      data: (rawData && rawData.body) || [],
      totalCount,
    };
  }
}
