import { Injectable } from '@angular/core';

@Injectable()
export class MessagesEncodingService {
  /**
   * Message base64 decode
   *
   * @param str Encoded string
   */
  b64Decode(str: string) {
    return decodeURIComponent(
      atob(str)
        .split('')
        .map(function (c) {
          return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
        })
        .join(''),
    );
  }

  /**
   * Message base64 encode
   *
   * @param str Decoded string
   */
  b64Encode(str: string) {
    return btoa(
      encodeURIComponent(str).replace(/%([0-9A-F]{2})/g, function (_match, p1) {
        return String.fromCharCode(parseInt(p1, 16));
      }),
    );
  }
}
