import { Injectable } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { BehaviorSubject, Observable } from 'rxjs';
import { ManipulationType, MessagesConfirmationModalModel } from './manipulation-confirm-modal-types.model';
import { SharedMethodsService } from '../../services/shared-methods/shared-methods.service';

@Injectable()
export class MessagesManipulationConfirmModalService {
  private readonly initialModalVal: MessagesConfirmationModalModel = {
    opened: false,
    type: undefined,
  };
  private readonly emitToggleModal = new BehaviorSubject(this.initialModalVal);

  /**
   * Toggle modal event emitter
   */
  public emitToggleModal$: Observable<MessagesConfirmationModalModel> = this.emitToggleModal.asObservable();

  constructor(private readonly shared: SharedMethodsService) {}

  /**
   * Toggle confirm modal
   *
   * @param shouldOpen Should open modal flag
   * @param type Manipulation type
   * @param form Form data
   * @param prevValue Previous value
   */
  toggleModal(
    shouldOpen: boolean,
    type?: ManipulationType | undefined,
    form?: FormGroup | undefined,
    prevValue?: { [key: string]: any },
  ) {
    let opened = shouldOpen;
    if (type === ManipulationType.discard) {
      opened = this.shared.isFormInvalid(form, prevValue);
    }

    this.emitToggleModal.next({ opened, type });
  }
}
