import { Observable } from 'rxjs';
import {
  DraftPostResponseBody,
  MessageDraftGetResponseBody,
  MessageDraftPutRequestBody,
  PresentationMessagePostResponseBody,
  TopicsGetResponseBody,
} from '@backbase/messages-v5-http-ang';

export abstract class BaseCreateMessageFormService<T> {
  abstract getTopics(): Observable<TopicsGetResponseBody[] | null>;
  abstract createMessage(params: T): Observable<PresentationMessagePostResponseBody | null>;
  abstract saveDraft?(params: T): Observable<DraftPostResponseBody | null>;
  abstract getDraft?(id: string): Observable<MessageDraftGetResponseBody | null>;
  abstract deleteDraft?(id: string): Observable<any>;
  abstract updateDraft?(id: string, body: MessageDraftPutRequestBody): Observable<any>;
}
