import { Component, EventEmitter, OnDestroy, Output } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { BehaviorSubject, combineLatest, EMPTY, Observable, Subject } from 'rxjs';
import { map, pluck, takeUntil } from 'rxjs/operators';
import { ConversationThread, MessageDraftsGetResponseBody } from '@backbase/messages-v5-http-ang';
import { SharedMethodsService } from '../../services/shared-methods/shared-methods.service';
import { ApplyFilterParams, FilterParams, MailboxType } from './model/params.model';
import { BaseConversationsListPropertiesService } from './base-conversations-list-properties.service';
@Component({
  template: '',
})
export class BaseConversationsListWidgetComponent implements OnDestroy {
  /**
   * Emits id of the selected conversation
   */
  @Output() conversationId = new EventEmitter();

  /**
   * Emits filter object to keep widget filter params
   */
  @Output() filter = new EventEmitter<string>();

  /**
   * The error response information.
   */
  errorResponse: HttpErrorResponse | undefined;

  /**
   * The `Unsubscribe` subject.
   */
  readonly unsubscribe$ = new Subject<boolean>();

  /**
   * The loading parameter indicating the starting page for the data to be loaded from.
   */
  readonly from$ = new BehaviorSubject(0);

  /**
   * The parameters object with the starting page number.
   */
  filterParams: FilterParams = {
    from: 0,
  };

  /**
   * The parameters subject with applied filters.
   */
  readonly filterParams$: Observable<FilterParams> = this.getRouteParam(this.activatedRoute, 'filter').pipe(
    takeUntil(this.unsubscribe$),
    map(params => {
      if (params) {
        try {
          return JSON.parse(params);
        } catch (err) {}
      }

      return {};
    }),
  );

  constructor(
    readonly shared: SharedMethodsService,
    readonly activatedRoute: ActivatedRoute,
    readonly properties: BaseConversationsListPropertiesService,
  ) {}

  /**
   * The method to change the page and load conversations corresponding to that page.
   *
   * @param page Page number
   */
  onPageChange(page?: number) {
    if (page || page === 0) {
      this.from$.next(page);
      this.filterParams.from = page;
      this.emitFilterParams(this.filterParams);
    }
  }

  /**
   * The method to switch to another mailbox type and load corresponding data.
   *
   * @param mailbox Mailbox type
   */
  onMailboxApply(mailbox: MailboxType) {
    this.from$.next(0);
    this.filterParams = this.setMailboxTypedParams(mailbox);
    this.emitFilterParams(this.filterParams);
  }

  /**
   * The method to fetch conversation list according to the applied filter parameters.
   *
   * @param obj Filter params
   */
  onFilterFormApply(obj: ApplyFilterParams) {
    this.from$.next(0);
    this.filterParams = this.setFilterFormParams(obj);
    this.emitFilterParams(this.filterParams);
  }

  /**
   * The method to open the draft thread.
   *
   * @param draft Draft data
   */
  openDraft(draft: ConversationThread | MessageDraftsGetResponseBody) {
    if ('conversationId' in draft) {
      this.conversationId.emit(draft.conversationId);
    } else {
      this.properties.createMessageOpenEventName
        .pipe(takeUntil(this.unsubscribe$))
        .subscribe(eventName => this.shared.eventBusPublish(eventName, { draftId: draft.id }));
    }
  }

  /**
   * The method to open the conversation thread.
   *
   * @param conversation Conversation data
   */
  openConversation(conversation: ConversationThread | string) {
    this.conversationId.emit(typeof conversation === 'string' ? conversation : conversation.id);
  }

  /**
   * The method to open the error notification.
   *
   * @param error Http error response
   */
  showErrorNotification(error: HttpErrorResponse) {
    this.shared.openNotification(this.shared.getErrorMessage(error), 'error');

    return EMPTY;
  }

  ngOnDestroy() {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  /**
   * The method to emit the filtering parameters.
   *
   * @param filterParams Filter params data
   */
  emitFilterParams(filterParams: FilterParams) {
    const params = JSON.stringify(filterParams);

    this.filter.emit(params);
  }

  private setMailboxTypedParams(mailboxType: string) {
    return {
      from: 0,
      mailboxType,
    };
  }

  private setFilterFormParams(filterParams: ApplyFilterParams) {
    return {
      from: 0,
      ...filterParams,
    };
  }

  private getRouteParam(route: ActivatedRoute, param: string): Observable<string | null> {
    const paramValue = route.paramMap.pipe(pluck<ParamMap, string | null>('params', param));

    if (!route.parent) {
      return paramValue;
    }

    return combineLatest([paramValue, this.getRouteParam(route.parent, param)]).pipe(
      map(params => params[0] || params[1]),
    );
  }
}
