import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BehaviorSubject, EMPTY, Observable, of, PartialObserver, Subject } from 'rxjs';
import { filter, finalize, first, map, switchMap, takeUntil } from 'rxjs/operators';
import { SharedMethodsService } from '../../../services/shared-methods/shared-methods.service';
import {
  ManipulationType,
  MessagesConfirmationModalModel,
} from '../../messages-manipulation-confirm-modal/manipulation-confirm-modal-types.model';
import { MessagesManipulationConfirmModalService } from '../../messages-manipulation-confirm-modal/messages-manipulation-confirm-modal.service';
import { BaseCreateMessageFormService } from './base-create-message-form.service';
import { MessageDraftGetResponseBody, TopicsGetResponseBody } from '@backbase/messages-v5-http-ang';
import { MessagesEncodingService } from '../../../services/messages-encoding/messages-encoding.service';
import { SimpleFormModel } from '../../../models/simple-form.model';
import { MessagesUploadAttachmentsService } from '../../upload-attachments/services/messages-upload-attachments.service';
import { FileObject } from '../../upload-attachments/model/messages-upload-attachments.model';

@Component({
  template: '',
  providers: [MessagesManipulationConfirmModalService, MessagesUploadAttachmentsService],
})
export class BaseCreateMessageFormComponent<T> implements OnInit, OnDestroy {
  /**
   * Input stream of reset signals that will trigger form reset.
   */
  @Input() reset: Observable<void> | undefined;

  /**
   * Input for maximum file attachment size in megabytes
   */
  @Input() maxAttachmentSize: string | undefined;

  /**
   * Input for maximum characters length for name field value
   */
  @Input() maxMailoutNameLength: number | undefined;

  /**
   * Input for maximum characters length for subject field value
   */
  @Input() maxSubjectLength: number | undefined;

  /**
   * Input for maximum characters length for message field value
   */
  @Input() maxMessageLength: number | undefined;

  /**
   * The parent modal open/closed state.
   */
  @Input() readonly isModalOpen: boolean | undefined;
  /**
   * Input draft id to get draft and prefill compose form with it
   */
  @Input()
  set draftId(value: string | undefined) {
    this.draftId$.next(value);
  }

  /**
   * The draft ID getter.
   */
  get draftId() {
    return this.draftId$.getValue();
  }

  /**
   * The event that's fired after confirm button is pressed.
   */
  @Output() confirm = new EventEmitter<string | null>();

  /**
   * The event that's fired after cancel button is pressed.
   */
  @Output() cancel = new EventEmitter<void>();

  /**
   * The notification for the successful compose message operation.
   */
  @ViewChild('messageComposeSuccess') messageComposeSuccessTpl: TemplateRef<any> | undefined;

  /**
   * The notification for the successfully saved draft.
   */
  @ViewChild('messageSaveDraftSuccess') messageSaveDraftSuccessTpl: TemplateRef<any> | undefined;

  /**
   * The notification for the successfully saved draft.
   */
  @ViewChild('messageUpdateDraftSuccess') messageUpdateDraftSuccessTpl: TemplateRef<any> | undefined;

  /**
   * The notification for the compose message error.
   */
  @ViewChild('messageComposeError') messageComposeErrorTpl: TemplateRef<any> | undefined;

  /**
   * The notification for the save draft error.
   */
  @ViewChild('messageSaveDraftError') messageSaveDraftErrorTpl: TemplateRef<any> | undefined;

  /**
   * The notification for the update draft error.
   */
  @ViewChild('messageUpdateDraftError') messageUpdateDraftErrorTpl: TemplateRef<any> | undefined;

  readonly hostRef = this;

  /**
   * The file attachments array.
   */
  attachments: Array<FileObject> | undefined;

  /**
   * The blank new message form with the initial values.
   */
  newMessageFormGroup: FormGroup;

  /**
   * The draft form value.
   */
  draftFormValue: { [key: string]: any } | undefined;

  /**
   * Confirmation modal parameters.
   */
  confirmationModal: MessagesConfirmationModalModel = {
    opened: false,
    type: undefined,
  };

  /**
   * The flag indicating if the form is loading.
   */
  formLoading = false;

  private readonly topics$ = new BehaviorSubject<TopicsGetResponseBody[] | null | undefined>(undefined);

  /**
   * The stream with topics.
   */
  readonly topics: Observable<TopicsGetResponseBody[] | null | undefined> = this.topics$.asObservable();

  /**
   * The manipulationType enum ( `resolve`, `unresolve`, `assign`, `unassign`, `discard`,
   * `discardDraft`, `discardPreview`, `deleteTopic`, `deleteMailout`)
   */
  readonly manipulationTypes = ManipulationType;
  private readonly unsubscribe$ = new Subject<undefined>();
  private readonly draftId$ = new BehaviorSubject<string | undefined>(undefined);

  constructor(
    readonly sharedService: SharedMethodsService,
    readonly confirmModalService: MessagesManipulationConfirmModalService,
    readonly cd: ChangeDetectorRef,
    readonly createMessageFormService: BaseCreateMessageFormService<T>,
    readonly fb: FormBuilder,
    readonly encodingService: MessagesEncodingService,
    readonly uploadService: MessagesUploadAttachmentsService,
  ) {
    this.newMessageFormGroup = this.fb.group({
      topic: ['', Validators.required],
      subject: ['', Validators.required],
      body: ['', Validators.required],
      attachments: [],
    });
  }

  ngOnInit() {
    if (this.reset) {
      this.reset.pipe(takeUntil(this.unsubscribe$)).subscribe(() => {
        this.confirmModalService.toggleModal(
          true,
          this.manipulationTypes.discard,
          this.newMessageFormGroup,
          this.draftFormValue,
        );
      });
    }

    this.createMessageFormService.getTopics().subscribe(res => {
      this.restoreTopicSelection(res);
      this.topics$.next(res);
    });

    this.draftId$
      .pipe(
        filter(id => !!(id && id.length)),
        switchMap(id => this.getDrafts(id)),
      )
      .subscribe();

    this.confirmModalService.emitToggleModal$.pipe(takeUntil(this.unsubscribe$)).subscribe(({ opened, type }) => {
      this.confirmationModal = { opened, type };
      if (!opened && type === ManipulationType.discard) {
        this.onCancel();
      }
      this.cd.detectChanges();
    });
  }

  /**
   * The method to get a simple value from the form.
   *
   * @param simpleFormModel Message data
   */
  getSimpleFormVal({ body, subject, topic, attachments }: SimpleFormModel) {
    return {
      ...(body && body.length && { body: this.encodingService.b64Encode(body) }),
      ...(subject && subject.length && { subject }),
      ...(topic && topic.id && { topic: topic.id }),
      ...(attachments && attachments.length && { attachments: attachments.map(item => item.id) }),
    };
  }

  resetFormOnElementBlur() {
    if (!this.isModalOpen) {
      setTimeout(() => {
        this.newMessageFormGroup.reset();
      }, 0);
    }
  }

  /**
   * The method to create a message.
   *
   * @param value Message data
   */
  createMessage(value: T) {
    this.formLoading = true;
    this.newMessageFormGroup.disable();

    this.createMessageFormService
      .createMessage(value)
      .pipe(
        first(),
        switchMap(() => {
          if (this.createMessageFormService.deleteDraft && this.draftId) {
            return this.createMessageFormService.deleteDraft(this.draftId);
          }

          return of(undefined);
        }),
        finalize(() => {
          this.formLoading = false;
        }),
      )
      .subscribe(this.responseHandler(this.messageComposeSuccessTpl, this.messageComposeErrorTpl));
  }

  /**
   * The method to save a message as a draft.
   *
   * @param value Draft data
   */
  onSave(value: T) {
    this.newMessageFormGroup.disable();

    if (
      this.sharedService.hasFormValue(this.newMessageFormGroup) &&
      this.sharedService.isFormInvalid(this.newMessageFormGroup, this.draftFormValue)
    ) {
      if (this.draftId) {
        this.updateDraft(this.draftId, value);
      } else {
        this.saveDraft(value);
      }
    } else {
      this.onCancel();
    }
  }

  /**
   * The method to get the draft and prefill the compose form with it.
   *
   * @param id Draft id
   */
  getDrafts(id?: string): Observable<MessageDraftGetResponseBody | null> {
    if (this.createMessageFormService.getDraft && id) {
      return this.createMessageFormService.getDraft(id).pipe(
        map(res => {
          const drafts = res || {};
          const formValue: any = {};
          const topics = this.topics$.getValue();

          for (const [key, value] of Object.entries(drafts)) {
            formValue[key] = value;

            if (this.newMessageFormGroup.controls.hasOwnProperty(key)) {
              formValue[key] = this.handleFormValues(key, value, topics);

              if (formValue[key]) {
                this.newMessageFormGroup.controls[key].setValue(formValue[key]);
              }
            }
          }

          this.draftFormValue = formValue;
          this.newMessageFormGroup.markAsPristine();

          return res;
        }),
      );
    }

    return EMPTY;
  }

  ngOnDestroy() {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  /**
   * The method to resolve the confirmation modal.
   */
  confirmConfirmationModal() {
    if (this.confirmationModal && this.confirmationModal.type === ManipulationType.discard) {
      this.onCancel();
    }

    this.confirmModalService.toggleModal(false);
  }

  /**
   * The method to cancel the confirmation modal.
   */
  onCancel() {
    this.resetToInit(true);
    this.cancel.emit();
  }

  /**
   * The method to get any errors generated by failing validation, or null if there are no errors.
   *
   * @param field Field name
   * @param type Error type
   */
  hasError(field: string, type: string): boolean {
    const control = this.getControl(field);

    return control?.errors?.[type];
  }

  /**
   * The method that defines whether the control is valid or not.
   *
   * @param field Field name
   * @returns Is form field valid flag
   */
  isInvalidControl(field: string): boolean {
    const control = this.getControl(field);

    return !!control && control.touched && control.invalid;
  }

  private saveDraft(value: T) {
    if (this.createMessageFormService.saveDraft) {
      this.createMessageFormService
        .saveDraft(value)
        .pipe(first())
        .subscribe(this.responseHandler(this.messageSaveDraftSuccessTpl, this.messageSaveDraftErrorTpl));
    }
  }

  private updateDraft(draftId: string, value: T) {
    if (this.createMessageFormService.updateDraft) {
      this.createMessageFormService
        .updateDraft(draftId, value)
        .pipe(first())
        .subscribe(this.responseHandler(this.messageUpdateDraftSuccessTpl, this.messageUpdateDraftErrorTpl));
    }
  }

  private handleFormValues(key: string, value: any, topics?: TopicsGetResponseBody[] | null) {
    if (key === 'body') {
      return this.encodingService.b64Decode(value as string);
    }

    if (key === 'topic' && topics) {
      const existingTopic = topics.find(topic => topic.id === value.id);

      return existingTopic ? existingTopic : '';
    }

    if (key === 'attachments') {
      this.attachments = [...value];
    }

    return value;
  }

  private responseHandler(successTpl: any, errorTpl: any): PartialObserver<any> {
    return {
      next: res => {
        this.sharedService.openNotification(successTpl ? successTpl : '');

        this.confirm.emit(res && res.id);
      },
      error: error => {
        const errorMessage = this.sharedService.getErrorMessage(error) || errorTpl;
        this.sharedService.openNotification(errorMessage, 'error');
        this.newMessageFormGroup.enable();
      },
      complete: () => this.resetToInit(),
    };
  }

  protected resetToInit(isFormDiscard = false) {
    this.newMessageFormGroup.enable();
    this.newMessageFormGroup.reset();
    this.draftFormValue = undefined;
    this.uploadService.reset(isFormDiscard);
  }

  private getControl(field: string): AbstractControl | undefined {
    return this.newMessageFormGroup && this.newMessageFormGroup.controls[field];
  }

  private restoreTopicSelection(topics: TopicsGetResponseBody[] | null) {
    topics?.map(topic => {
      if (topic.id === this.newMessageFormGroup.controls['topic']?.value?.id) {
        this.newMessageFormGroup.controls['topic'].setValue(topic);
      }
    });
  }
}
